import { getCurrentUser } from '@/lib/auth';
import { sql } from '@vercel/postgres';
import { NextRequest, NextResponse } from 'next/server';

function calculateScore(questions: any[], answers: any[]): number {
  let score = 0;
  const maxScore = questions.length * 100;

  questions.forEach((question, index) => {
    const studentAnswer = answers[index];

    if (question.type === 'single' || question.type === 'multi') {
      if (JSON.stringify(studentAnswer) === JSON.stringify(question.correct_answer)) {
        score += 100;
      }
    }
  });

  return Math.round((score / maxScore) * 100);
}

export async function POST(request: NextRequest, { params }: { params: Promise<{ id: string }> }) {
  try {
    const user = await getCurrentUser();

    if (!user || user.role !== 'student') {
      return NextResponse.json({ success: false, error: 'Only students can submit quizzes' }, { status: 403 });
    }

    const { id } = await params;
    const { attemptId, answers } = await request.json();

    // Get quiz questions to calculate score
    const quizResult = await sql`
      SELECT questions FROM quizzes WHERE id = ${id}
    `;

    const questions = JSON.parse(quizResult.rows[0].questions);
    const score = calculateScore(questions, answers);

    const result = await sql`
      UPDATE quiz_attempts 
      SET answers = ${JSON.stringify(answers)}, score = ${score}, finished_at = NOW()
      WHERE id = ${attemptId} AND student_id = ${user.userId}
      RETURNING id, quiz_id, student_id, score, finished_at
    `;

    return NextResponse.json({ success: true, data: { ...result.rows[0], score } });
  } catch (error) {
    return NextResponse.json({ success: false, error: 'Failed to submit quiz' }, { status: 500 });
  }
}
